  background-color: white;
  padding: 10px;
  border-radius: 5px;
  width: 100px;
  text-align: center;
}

.add-todo-add-button {
  background-color: rgb(90, 177, 90);
}

.add-todo-cancel-button {
  background-color: rgb(209, 101, 101);
}

.add-todo-add-button:hover, .add-todo-cancel-button:hover {
  transform: scale(1.1);
  cursor: pointer;
